import React from 'react';
import { CardImgContainer } from './CardsElements.styles';
import { OpposedDivContainer } from './CardsElements.styles';
import { CARDS_DETAILS_NAMESPACE } from '../../features/Cards/constants';
import LockedCardSvg from '../../assets/SvgComponents/LockedCard/LockedCard';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';

const CardsElements = props => {
  let currentImage = null;
  try {
    currentImage = require(`../../assets/img/CardsImg/${props.productCode}.png`);
  } catch (error) {
    currentImage = require(`../../assets/img/CardsImg/defaultCard.png`);
  }
  return (
    <CardImgContainer
      key={props.key}
      cardActive={props.isActive}
      onClick={props.onPickCard}
    >
      <OpposedDivContainer cardOpposed={props.isOpposed}>
        <LockedCardSvg />
      </OpposedDivContainer>
      <img src={currentImage} alt="card" />
      <span className="numcompte"> {props.cardNumber}</span>
      <span className="nom">{props.ownerName}</span>
      <span className="date">
        {t(`${CARDS_DETAILS_NAMESPACE}expiryAbbreviation`)} {props.expiryDate}
      </span>
    </CardImgContainer>
  );
};

export default withTranslation()(CardsElements);
