import React from 'react';
import { connect } from 'react-redux';
import CardsCaroussel from '../../../components/CardsCaroussel/CardsCaroussel';
import CardActions from '../../../components/CardActions/CardActions';
import CardPanel from '../../Cards/components/cardPanel';
import {
  getCards,
  selectCard,
  oppose,
  switchCardState,
} from '../../../redux/cards/cards.actions';
import {
  showMessage,
  hideMessage,
} from '../../../redux/messages/messages.actions';
import {
  getSelectedCard,
  getSelectedCardBalance,
} from '../../../redux/cards/cards.selectors';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { CARDS_DETAILS_NAMESPACE } from '../constants';

class CardsDetails extends React.PureComponent {
  componentDidMount() {
    this.props.getCards();
  }
  _cardChangeHandler = selectedCardIndex => {
    if (this.props.cards.length > 0) {
      this.props.selectCard(this.props.cards[selectedCardIndex].cardNumber);
    }
  };
  _opposeCard = () => {
    this.props.opposeCard();
    this.props.hideMessage();
  };
  _switchCardState = () => {
    this.props.switchCardState();
    this.props.hideMessage();
  };
  _renderEmptyCardsMessage = () =>
    this.props.cards.length === 0 ? (
      <div className="cardempty">
        <div className="text-warning">
          <span className="modal-main-icon mdi mdi-alert-triangle" />
        </div>
        {t(`${CARDS_DETAILS_NAMESPACE}noCardsAvailable`)}
      </div>
    ) : null;
  render() {
    let statusLabel = null;
    let confirmMessage = null;
    if (this.props.selectedCard) {
      statusLabel = this.props.selectedCard.isActive
        ? t(`${CARDS_DETAILS_NAMESPACE}deactivate`)
        : t(`${CARDS_DETAILS_NAMESPACE}activate`);
      confirmMessage = this.props.selectedCard.isActive
        ? t(`${CARDS_DETAILS_NAMESPACE}deactivateConfirm`)
        : t(`${CARDS_DETAILS_NAMESPACE}activateConfirm`);
    }
    const actionTab = this.props.selectedCard &&
      !this.props.selectedCard.isOpposed && (
        <CardActions
          statusLabel={statusLabel}
          cardActivated={this.props.selectedCard.isActive}
          balance={this.props.selectedCardBalance}
          onSwitchchange={() => {
            this.props.showMessage(
              `${confirmMessage} ${this.props.selectedCardNumber}`,
              'WARN',
              t(`${CARDS_DETAILS_NAMESPACE}confirm`),
              true,
              this._switchCardState,
            );
          }}
          onOpposeClick={() => {
            this.props.showMessage(
              `${t(`${CARDS_DETAILS_NAMESPACE}oppositionConfirm`)} ${
                this.props.selectedCardNumber
              }`,
              'DANG',
              t(`${CARDS_DETAILS_NAMESPACE}confirm`),
              true,
              this._opposeCard,
            );
          }}
        />
      );
    return this.props.cards ? (
      <div className="row mx-auto">
        <div className="col-sm-12">
          <div className="panel-body pl-0 pr-0 carte__page">
            <div className="position-relative">
              {actionTab}
              <CardsCaroussel
                cards={this.props.cards}
                selectCard={this._cardChangeHandler}
              />
              {this._renderEmptyCardsMessage()}
            </div>
            <CardPanel show={this.props.cards.length > 0} />
          </div>
        </div>
      </div>
    ) : null;
  }
}

const mapDispatchToProps = dispatch => ({
  getCards: () => {
    dispatch(getCards());
  },
  selectCard: cardNumber => {
    dispatch(selectCard(cardNumber));
  },
  opposeCard: () => {
    dispatch(oppose());
  },
  switchCardState: () => {
    dispatch(switchCardState());
  },
  showMessage: (
    text,
    severity,
    validateTitle,
    hasCancelBtn,
    handleValidate,
  ) => {
    dispatch(
      showMessage(text, severity, validateTitle, hasCancelBtn, handleValidate),
    );
  },
  hideMessage: () => {
    dispatch(hideMessage());
  },
});

const mapStateToProps = state => ({
  cards: state.Cards.cards,
  selectedCardNumber: state.Cards.selectedCardNumber,
  selectedCard: getSelectedCard(state),
  selectedCardBalance: getSelectedCardBalance(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(CardsDetails),
);
