import React from 'react';
import { connect } from 'react-redux';
import DataTable from '../../../components/DataTable/CustomDataTable';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import FormatDate from '../../../components/FormattedDate/FormattedDate';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import DateCalender from '../../../components/DateCalendar/DateCalendar';
import CardsHistoryTemplate from '../../../pdf/Templates/CardsHistoryTemplate';
import Panel from '../../../components/Panel/Panel';
import CardsHistoryTemplateExcel from '../../../excel/Templates/CardsHistoryTemplateExcel';
import { dateFilter } from '../../../utils/DateUtils';
import { getCardTransactions } from '../../../redux/cards/cards.actions';
import {
  exportTableToPdf,
  exportTableToExcel,
} from '../../../utils/exportUtils';
import { getSelectedAccountNumber } from '../../../redux/accounts/accounts.selectors';
import { HISTORY_TAB_NAMESPACE } from '../constants';
import { getSelectedCard } from '../../../redux/cards/cards.selectors';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';

class historyTab extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      maxDate: new Date(),
    };
  }
  componentDidMount() {
    this.props.getCardTransactions();
  }
  componentDidUpdate(prevProps, prevState, snapShot) {
    if (prevProps.selectedCard !== this.props.selectedCard)
      this.props.getCardTransactions();
  }
  _buildDataTableRows = () =>
    this.props.cardTransactions.map(transaction => {
      const {
        transactionDate,
        transactionType,
        merchantDescription,
        billingAmount,
      } = transaction;
      return {
        date: transactionDate,
        description: transactionType,
        trader: merchantDescription,
        amount: billingAmount,
      };
    });
  _dateTemplate = rowData => (
    <FormatDate
      dateTime={rowData.date}
      format={FormatsConfiguration.DateTimeFormat.fullDate}
    />
  );
  _balanceFormatter = data => (
    <FormattedNumber
      number={data.amount}
      format={FormatsConfiguration.AmountFormat}
      currency={t('Components:currency')}
    />
  );
  _dateCalendarComponent = maxDate => (
    <DateCalender
      maxDate={maxDate}
      handleDateChange={e => {
        this.dt.filter(e.value, 'date', 'custom');
        this.forceUpdate();
      }}
    />
  );
  _getPdfFileName = () =>
    `${t(`${HISTORY_TAB_NAMESPACE}history`)}_${
      this.props.selectedAccountNumber
    }.pdf`;
  _getSheetName = () =>
    `${t(`${HISTORY_TAB_NAMESPACE}history`)}_${
      this.props.selectedAccountNumber
    }`;
  render() {
    let rows = this._buildDataTableRows();
    return (
      <div>
        <CardsHistoryTemplateExcel tableRows={rows} />
        <CardsHistoryTemplate tableRows={rows} />
        <Panel
          title={t(`${HISTORY_TAB_NAMESPACE}cardOperationsHistory`)}
          hasDownloadLink
          pdfLinkAction={() =>
            exportTableToPdf(
              'CardOperationsHistorySummary',
              'CardOperationsHistoryLogo',
              'CardOperationsHistoryData',
              this._getPdfFileName(),
            )
          }
          csvLinkAction={() =>
            exportTableToExcel('Excel', this._getSheetName())
          }
        />
        <DataTable
          value={rows}
          onRowReorder={e => (rows = e.value)}
          reference={el => (this.dt = el)}
          columnResizeMode="fit"
          cols={[
            {
              field: 'date',
              header: t(`${HISTORY_TAB_NAMESPACE}date`),
              body: this._dateTemplate,
              filter: true,
              filterElement: this._dateCalendarComponent(this.state.maxDate),
              filterFunction: dateFilter,
            },
            {
              field: 'description',
              header: t(`${HISTORY_TAB_NAMESPACE}description`),
            },
            {
              field: 'trader',
              header: t(`${HISTORY_TAB_NAMESPACE}merchant`),
            },
            {
              field: 'amount',
              header: t(`${HISTORY_TAB_NAMESPACE}amount`, {
                currency: t('Components:currency'),
              }),
              body: this._balanceFormatter,
            },
          ]}
        />
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => ({
  getCardTransactions: () => {
    dispatch(getCardTransactions());
  },
});

const mapStateToProps = state => ({
  selectedAccountNumber: getSelectedAccountNumber(state),
  cardTransactions: state.Cards.cardTransactions,
  selectedCard: getSelectedCard(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(historyTab),
);
