import React from 'react';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import * as DateUtils from '../../utils/DateUtils';
import FormatsConfiguration from '../../utils/FormatsConfiguration';
import FormattedNumber from '../../components/FormattedNumber/FormattedNumber';
import Logo from '../../components/Logo/PNGLogo';
import { pdfStyles } from './PdfTemplate.styles';
import { HISTORY_TAB_NAMESPACE } from '../../features/Cards/constants';

const CardsHistoryTemplate = props => {
  const operations = props.tableRows.map(row => (
    <tr key={row.code}>
      <td>
        {DateUtils.formatDateTime(
          row.date,
          FormatsConfiguration.DateTimeFormat.fullDate,
        )}
      </td>
      <td>{row.description}</td>
      <td>{row.trader}</td>
      <td>
        <FormattedNumber
          number={row.amount}
          format={FormatsConfiguration.AmountFormat}
        />
      </td>
    </tr>
  ));

  return (
    <div id="pdf" style={pdfStyles.pdf}>
      <table id="CardOperationsHistoryLogo">
        <tbody>
          <tr>
            <td>
              <Logo />
            </td>
          </tr>
        </tbody>
      </table>
      <table id="CardOperationsHistorySummary">
        <tbody>
          <tr>
            <td></td>
            <td>
              <b>{t(`${HISTORY_TAB_NAMESPACE}cardOperationsHistory`)}</b>
            </td>
          </tr>
        </tbody>
      </table>
      <table id="CardOperationsHistoryData">
        <thead>
          <tr>
            <th>{t(`${HISTORY_TAB_NAMESPACE}operationDate`)}</th>
            <th>{t(`${HISTORY_TAB_NAMESPACE}description`)}</th>
            <th>{t(`${HISTORY_TAB_NAMESPACE}merchant`)}</th>
            <th>
              {t(`${HISTORY_TAB_NAMESPACE}amount`, {
                currency: t('Components:currency'),
              })}
            </th>
          </tr>
        </thead>
        <tbody>{operations}</tbody>
      </table>
    </div>
  );
};

export default withTranslation()(CardsHistoryTemplate);
