import { connect } from 'react-redux';
import AccountCards from './components/feature';
import { activateCardsTab } from '../../redux/menu/menu.actions';

const mapStateToProps = state => ({
  activeTab: state.Menu.cardsActiveTab,
});
const mapDispatchToProps = dispatch => ({
  activateTab: activeTab => {
    dispatch(activateCardsTab(activeTab));
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AccountCards);
