import styled from 'styled-components';
export const CardImgContainer = styled.div.attrs(props => ({
  className: `image ${props.cardActive ? '' : 'not-active'}`,
}))``;
export const OpposedDivContainer = styled.div.attrs(props => ({
  className: `card-oppos ${props.cardOpposed ? '' : 'd-none'}`,
}))``;
