import React from 'react';
import Coverflow from '../Caroussel/Caroussel';
import CardsElement from '../CardsElements/CardsElements';
import { withTranslation } from 'react-i18next';

const CardsCaroussel = props => {
  const _renderCards = () =>
    props.cards.map((card, index) => (
      <CardsElement
        key={card.cardNumber}
        isActive={card.isActive}
        isOpposed={card.isOpposed}
        onPickCard={() => {
          props.selectCard(index);
        }}
        cardNumber={card.cardNumber}
        ownerName={card.ownerName}
        expiryDate={card.formattedExpiryDate}
        productCode={card.produitCarte}
      />
    ));
  return (
    <Coverflow
      width="1000"
      height="450"
      displayQuantityOfSide={3}
      navigation={false}
      enableScroll={true}
      clickable={true}
      enableHeading={false}
      currentFigureScale="1.9"
      handleActive={current => props.selectCard(current)}
    >
      {_renderCards()}
    </Coverflow>
  );
};

export default withTranslation()(CardsCaroussel);
