import React from 'react';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import { Slider } from 'primereact/slider';
import { InputText } from 'primereact/inputtext';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { LIMITS_TAB_NAMESPACE } from '../constants';
import FormModal from '../../../components/Ui/CustomModal/FormModal';
import { editLimit } from '../../../redux/cards/cards.actions';

class EditLimitModal extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      dailyInternAmount: this.props.limitToEdit.onDailyOnusAmnt,
      periodicInternAmount: this.props.limitToEdit.onPerOnusAmnt,
      dailyNationalAmount: this.props.limitToEdit.onDailyNatAmnt,
      periodicNationalAmount: this.props.limitToEdit.onPerNatAmnt,
      dailyInternationalAmount: this.props.limitToEdit.onDailyInternatAmnt,
      periodicInternationalAmount: this.props.limitToEdit.onPerInternatAmnt,
    };
    this.formModalRef = null;
  }

  _setFormModalRef = ref => {
    this.formModalRef = ref;
  };
  render() {
    const {
      onDailyOnusAmntMin,
      onDailyOnusAmntMax,
      onPerOnusAmntMin,
      onPerOnusAmntMax,
      onDailyNatAmntMin,
      onDailyNatAmntMax,
      onPerNatAmntMin,
      onPerNatAmntMax,
      onDailyInternatAmntMin,
      onDailyInternatAmntMax,
      onPerInternatAmntMin,
      onPerInternatAmntMax,
      processingCode,
    } = this.props.limitToEdit;
    const periodicity = this.props.cardLimitsPeriod;
    const initialLimits = {
      onDailyOnusAmnt: this.state.dailyInternAmount,
      onPerOnusAmnt: this.state.periodicInternAmount,
      onDailyNatAmnt: this.state.dailyNationalAmount,
      onPerNatAmnt: this.state.periodicNationalAmount,
      onDailyInternatAmnt: this.state.dailyInternationalAmount,
      onPerInternatAmnt: this.state.periodicInternationalAmount,
      processingCode: processingCode,
    };
    return (
      <FormModal
        title={`${t(`${LIMITS_TAB_NAMESPACE}editLimit`)} ${t(
          `${LIMITS_TAB_NAMESPACE}service${processingCode}`,
        )}`}
        show={this.props.show}
        handleClose={this.props.handleClose}
        handleValidate={() => this.formModalRef.submitForm()}
        hasValidateBtn
        validateTitle={t(`${LIMITS_TAB_NAMESPACE}validate`)}
        hasCancelBtn
        cancelTitle={t(`${LIMITS_TAB_NAMESPACE}cancel`)}
      >
        <Formik
          ref={ref => this._setFormModalRef(ref)}
          initialValues={initialLimits}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            this.props.editLimit(values);
            this.props.handleClose();
          }}
        >
          {({ handleSubmit, setFieldValue }) => (
            <form className="formulaire-plafonds" onSubmit={handleSubmit}>
              <h3>
                {t(`${LIMITS_TAB_NAMESPACE}internLimit`, {
                  currency: t('Components:currency'),
                })}
              </h3>
              <div className="form-group simulateur_plafons">
                <div className="simulator_item">
                  <div className="col-xs-12">
                    <h4>{t(`${LIMITS_TAB_NAMESPACE}daily`)}</h4>
                  </div>
                  <div className="col-xs-12 simu_value">
                    <span className="min-price">{onDailyOnusAmntMin}</span>
                    <Slider
                      value={this.state.dailyInternAmount}
                      min={parseInt(onDailyOnusAmntMin)}
                      max={parseInt(onDailyOnusAmntMax)}
                      onChange={e => {
                        setFieldValue('onDailyOnusAmnt', e.value.toString());
                        this.setState({ dailyInternAmount: e.value });
                      }}
                    />
                    <span className="max-price">{onDailyOnusAmntMax}</span>
                    <div className="value-text">
                      <InputText
                        onInput={e => {
                          if (
                            e.target.value >= parseInt(onDailyOnusAmntMin) &&
                            e.target.value <= parseInt(onDailyOnusAmntMax)
                          ) {
                            setFieldValue('onDailyOnusAmnt', e.target.value);
                            this.setState({
                              dailyInternAmount: e.target.value,
                            });
                          }
                        }}
                        value={this.state.dailyInternAmount}
                        type="number"
                      />
                      <div className="devise">{t('Components:currency')}</div>
                    </div>
                  </div>
                  <div className="col-xs-12">
                    <h4>{periodicity}</h4>
                  </div>
                  <div className="col-xs-12 simu_value">
                    <span className="min-price">{onPerOnusAmntMin}</span>
                    <Slider
                      value={this.state.periodicInternAmount}
                      min={parseInt(onPerOnusAmntMin)}
                      max={parseInt(onPerOnusAmntMax)}
                      onChange={e => {
                        setFieldValue('onPerOnusAmnt', e.value.toString());
                        this.setState({ periodicInternAmount: e.value });
                      }}
                    />
                    <span className="max-price">{onPerOnusAmntMax}</span>
                    <div className="value-text">
                      <InputText
                        onInput={e => {
                          if (
                            e.target.value >= parseInt(onPerOnusAmntMin) &&
                            e.target.value <= parseInt(onPerOnusAmntMax)
                          ) {
                            setFieldValue('onPerOnusAmnt', e.target.value);
                            this.setState({
                              periodicInternAmount: e.target.value,
                            });
                          }
                        }}
                        value={this.state.periodicInternAmount}
                        type="number"
                      />
                      <div className="devise">{t('Components:currency')}</div>
                    </div>
                  </div>
                </div>
              </div>
              <h3>
                {t(`${LIMITS_TAB_NAMESPACE}nationalLimit`, {
                  currency: t('Components:currency'),
                })}
              </h3>
              <div className="form-group simulateur_plafons">
                <div className="simulator_item">
                  <div className="col-xs-12">
                    <h4>{t(`${LIMITS_TAB_NAMESPACE}daily`)}</h4>
                  </div>
                  <div className="col-xs-12 simu_value">
                    <span className="min-price">{onDailyNatAmntMin}</span>
                    <Slider
                      value={this.state.dailyNationalAmount}
                      min={parseInt(onDailyNatAmntMin)}
                      max={parseInt(onDailyNatAmntMax)}
                      onChange={e => {
                        setFieldValue('onDailyNatAmnt', e.value.toString());
                        this.setState({ dailyNationalAmount: e.value });
                      }}
                    />
                    <span className="max-price">{onDailyNatAmntMax}</span>
                    <div className="value-text">
                      <InputText
                        onInput={e => {
                          if (
                            e.target.value >= parseInt(onDailyNatAmntMin) &&
                            e.target.value <= parseInt(onDailyNatAmntMax)
                          ) {
                            setFieldValue('onDailyNatAmnt', e.target.value);
                            this.setState({
                              dailyNationalAmount: e.target.value,
                            });
                          }
                        }}
                        value={this.state.dailyNationalAmount}
                        type="number"
                      />
                      <div className="devise">{t('Components:currency')}</div>
                    </div>
                  </div>
                  <div className="col-xs-12">
                    <h4>{periodicity}</h4>
                  </div>
                  <div className="col-xs-12 simu_value">
                    <span className="min-price">{onPerNatAmntMin}</span>
                    <Slider
                      value={this.state.periodicNationalAmount}
                      min={parseInt(onPerNatAmntMin)}
                      max={parseInt(onPerNatAmntMax)}
                      onChange={e => {
                        setFieldValue('onPerNatAmnt', e.value.toString());
                        this.setState({ periodicNationalAmount: e.value });
                      }}
                    />
                    <span className="max-price">{onPerNatAmntMax}</span>
                    <div className="value-text">
                      <InputText
                        onInput={e => {
                          if (
                            e.target.value >= parseInt(onPerNatAmntMin) &&
                            e.target.value <= parseInt(onPerNatAmntMax)
                          ) {
                            setFieldValue('onPerNatAmnt', e.target.value);
                            this.setState({
                              periodicNationalAmount: e.target.value,
                            });
                          }
                        }}
                        value={this.state.periodicNationalAmount}
                        type="number"
                      />
                      <div className="devise">{t('Components:currency')}</div>
                    </div>
                  </div>
                </div>
              </div>
              <h3>
                {t(`${LIMITS_TAB_NAMESPACE}internationalLimit`, {
                  currency: t('Components:currency'),
                })}
              </h3>
              <div className="form-group simulateur_plafons">
                <div className="simulator_item">
                  <div className="col-xs-12">
                    <h4>{t(`${LIMITS_TAB_NAMESPACE}daily`)}</h4>
                  </div>
                  <div className="col-xs-12 simu_value">
                    <span className="min-price">{onDailyInternatAmntMin}</span>
                    <Slider
                      value={this.state.dailyInternationalAmount}
                      min={parseInt(onDailyInternatAmntMin)}
                      max={parseInt(onDailyInternatAmntMax)}
                      onChange={e => {
                        setFieldValue(
                          'onDailyInternatAmnt',
                          e.value.toString(),
                        );
                        this.setState({ dailyInternationalAmount: e.value });
                      }}
                    />
                    <span className="max-price">{onDailyInternatAmntMax}</span>
                    <div className="value-text">
                      <InputText
                        onInput={e => {
                          if (
                            e.target.value >=
                              parseInt(onDailyInternatAmntMin) &&
                            e.target.value <= parseInt(onDailyInternatAmntMax)
                          ) {
                            setFieldValue(
                              'onDailyInternatAmnt',
                              e.target.value,
                            );
                            this.setState({
                              dailyInternationalAmount: e.target.value,
                            });
                          }
                        }}
                        value={this.state.dailyInternationalAmount}
                        type="number"
                      />
                      <div className="devise">{t('Components:currency')}</div>
                    </div>
                  </div>
                  <div className="col-xs-12">
                    <h4>{periodicity}</h4>
                  </div>
                  <div className="col-xs-12 simu_value">
                    <span className="min-price">{onPerInternatAmntMin}</span>
                    <Slider
                      value={this.state.periodicInternationalAmount}
                      min={parseInt(onPerInternatAmntMin)}
                      max={parseInt(onPerInternatAmntMax)}
                      onChange={e => {
                        setFieldValue('onPerInternatAmnt', e.value.toString());
                        this.setState({ periodicInternationalAmount: e.value });
                      }}
                    />
                    <span className="max-price">{onPerInternatAmntMax}</span>
                    <div className="value-text">
                      <InputText
                        onInput={e => {
                          if (
                            e.target.value >= parseInt(onPerInternatAmntMin) &&
                            e.target.value <= parseInt(onPerInternatAmntMax)
                          ) {
                            setFieldValue('onPerInternatAmnt', e.target.value);
                            this.setState({
                              periodicInternationalAmount: e.target.value,
                            });
                          }
                        }}
                        value={this.state.periodicInternationalAmount}
                        type="number"
                      />
                      <div className="devise">{t('Components:currency')}</div>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          )}
        </Formik>
      </FormModal>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  editLimit: newLimit => {
    dispatch(editLimit(newLimit));
  },
});

export default withTranslation()(
  connect(
    null,
    mapDispatchToProps,
  )(EditLimitModal),
);
