import React from 'react';
import { Link } from 'react-router-dom';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import { CARDS_DETAILS_NAMESPACE } from '../../features/Cards/constants';

class TabPanel extends React.Component {
  onClick = () => {
    const { label, onClick } = this.props;
    onClick(label);
  };

  render() {
    const {
      onClick,
      props: { activeTab, label },
    } = this;
    const className = activeTab === label ? 'active' : '';
    return (
      <li className={className} onClick={onClick}>
        <Link>
          <span className="icon">
            <img
              src={require(`../../assets/img/cardsTabsIcons/${label}.svg`)}
              alt={t(`${CARDS_DETAILS_NAMESPACE}${label}`)}
              width="20"
            />
          </span>
          {t(`${CARDS_DETAILS_NAMESPACE}${label}`)}
        </Link>
      </li>
    );
  }
}
export default withTranslation()(TabPanel);
