import { actionCreator } from '../../utils/generators';
import types from './cards.types';

export const getCards = () => ({
  type: actionCreator('req', types.GET_CARDS),
});
export const selectCard = selectedCardNumber => ({
  type: actionCreator('req', types.SELECT_CARD),
  payload: selectedCardNumber,
});
export const getCardTransactions = () => ({
  type: actionCreator('req', types.GET_TRANSACTIONS),
});
export const switchCardState = () => ({
  type: actionCreator('req', types.SWITCH_STATE),
});
export const oppose = () => ({
  type: actionCreator('req', types.OPPOSE),
});
export const getLimits = () => ({
  type: actionCreator('req', types.GET_LIMITS),
});
export const editLimit = newLimit => ({
  type: actionCreator('req', types.EDIT_LIMIT),
  payload: newLimit,
});
