import React from 'react';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { DETAILS_TAB_NAMESPACE } from '../constants';
import { connect } from 'react-redux';
import { getSelectedCard } from '../../../redux/cards/cards.selectors';
import Spinner from '../../../assets/SvgComponents/TextSpinner/TextSpinner';

const DetailsTab = props => {
  const selectedCard = props.selectedCard || {
    accountNumber: <Spinner />,
    ownerName: <Spinner />,
    deliveryCardDate: <Spinner />,
    validationStartDate: <Spinner />,
    expiryDate: <Spinner />,
  };
  const getSelectedCardStatus = () => {
    if (props.selectedCard) {
      if (props.selectedCard.isOpposed)
        return t(`${DETAILS_TAB_NAMESPACE}opposed`);
      return props.selectedCard.isActive
        ? t(`${DETAILS_TAB_NAMESPACE}active`)
        : t(`${DETAILS_TAB_NAMESPACE}deactivated`);
    }
    return <Spinner />;
  };
  return (
    <div className="tab-pane cont active">
      <div className="col-left">
        <div className="row-type">
          <label>{t(`${DETAILS_TAB_NAMESPACE}accountNumber`)}</label>
          <strong>{selectedCard.accountNumber}</strong>
        </div>
        <div className="row-type">
          <label>{t(`${DETAILS_TAB_NAMESPACE}holder`)}</label>
          <strong>{selectedCard.ownerName}</strong>
        </div>
        <div className="row-type">
          <label>{t(`${DETAILS_TAB_NAMESPACE}status`)}</label>
          <strong>{getSelectedCardStatus()}</strong>
        </div>
      </div>
      <div className="col-right">
        <div className="row-type">
          <label>{t(`${DETAILS_TAB_NAMESPACE}deliveryDate`)}</label>
          <strong>{selectedCard.deliveryCardDate}</strong>
        </div>
        <div className="row-type">
          <label>{t(`${DETAILS_TAB_NAMESPACE}validityStart`)}</label>
          <strong>{selectedCard.validationStartDate}</strong>
        </div>
        <div className="row-type">
          <label>{t(`${DETAILS_TAB_NAMESPACE}expiryDate`)}</label>
          <strong>{selectedCard.expiryDate}</strong>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => ({
  selectedCard: getSelectedCard(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    null,
  )(DetailsTab),
);
