import React from 'react';
import { connect } from 'react-redux';
import { getLimits } from '../../../redux/cards/cards.actions';
import { LIMITS_TAB_NAMESPACE } from '../constants';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { getSelectedCard } from '../../../redux/cards/cards.selectors';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import EditLimitModal from './EditLimitModal';

class LimitsTab extends React.PureComponent {
  state = { showEditModal: false, limitToEdit: null };
  componentDidMount() {
    this.props.getLimits();
  }
  componentDidUpdate(prevProps, prevState, snapShot) {
    if (prevProps.selectedCard !== this.props.selectedCard)
      this.props.getLimits();
  }
  _toggleEditLimitModal = limitToEdit => {
    this.setState({
      showEditModal: !this.state.showEditModal,
      limitToEdit: limitToEdit,
    });
  };
  _renderCardsLimitsRows = limits =>
    limits.length > 0
      ? limits.map(limit => {
          const limitedService = limit.processingCode
            ? t(`${LIMITS_TAB_NAMESPACE}service${limit.processingCode}`)
            : null;
          return (
            <tr key={limit.processingCode}>
              <td className="pl23">{limitedService}</td>
              <td className="pl20 align_right">
                <FormattedNumber
                  number={limit.onDailyOnusAmnt}
                  format={FormatsConfiguration.AmountFormat}
                />
              </td>
              <td className="pl20 align_right">
                <FormattedNumber
                  number={limit.onPerOnusAmnt}
                  format={FormatsConfiguration.AmountFormat}
                />
              </td>
              <td className="pl20 align_right">
                <FormattedNumber
                  number={limit.onDailyNatAmnt}
                  format={FormatsConfiguration.AmountFormat}
                />
              </td>
              <td className="pl20 align_right">
                <FormattedNumber
                  number={limit.onPerNatAmnt}
                  format={FormatsConfiguration.AmountFormat}
                />
              </td>
              <td className="pl20 align_right">
                <FormattedNumber
                  number={limit.onDailyInternatAmnt}
                  format={FormatsConfiguration.AmountFormat}
                />
              </td>
              <td className="pl20 align_right">
                <FormattedNumber
                  number={limit.onPerInternatAmnt}
                  format={FormatsConfiguration.AmountFormat}
                />
              </td>
              <td className="pl20 align_right">
                <button
                  className="btn btn-clear text-alert btn-sm"
                  disabled={this.props.selectedCard.isOpposed}
                  onClick={() => this._toggleEditLimitModal(limit)}
                >
                  <span
                    className="md-trigger popin_btn editer"
                    data-modal="editplafont"
                  >
                    {t(`${LIMITS_TAB_NAMESPACE}edit`)}
                  </span>
                </button>
              </td>
            </tr>
          );
        })
      : null;

  render() {
    const { periodicityCode, limits } = this.props.cardLimits;
    const cardLimitsPeriod = periodicityCode
      ? t(`${LIMITS_TAB_NAMESPACE}periodicity${periodicityCode}`)
      : null;
    return (
      <div className="tab-pane cont active">
        <div className="plafonds_wrraper">
          <table className="table-striped dataTable plafond-table">
            <thead>
              <tr>
                <th className="pl23" rowSpan="2" width="187">
                  {t(`${LIMITS_TAB_NAMESPACE}service`)}
                </th>
                <th className="text-center" colSpan="2" width="209">
                  {t(`${LIMITS_TAB_NAMESPACE}internLimit`, {
                    currency: t('Components:currency'),
                  })}
                </th>
                <th className="text-center" colSpan="2" width="209">
                  {t(`${LIMITS_TAB_NAMESPACE}nationalLimit`, {
                    currency: t('Components:currency'),
                  })}
                </th>
                <th className="text-center" colSpan="2">
                  {t(`${LIMITS_TAB_NAMESPACE}internationalLimit`, {
                    currency: t('Components:currency'),
                  })}
                </th>
                <th className="pl23" rowSpan="2" width="187">
                  {t(`${LIMITS_TAB_NAMESPACE}actions`)}
                </th>
              </tr>
              <tr>
                <th className="pl20"> {t(`${LIMITS_TAB_NAMESPACE}daily`)}</th>
                <th className="pl20">{cardLimitsPeriod}</th>
                <th className="pl20"> {t(`${LIMITS_TAB_NAMESPACE}daily`)}</th>
                <th className="pl20"> {cardLimitsPeriod}</th>
                <th className="pl20"> {t(`${LIMITS_TAB_NAMESPACE}daily`)}</th>
                <th className="pl20"> {cardLimitsPeriod}</th>
              </tr>
            </thead>
            <tbody>{this._renderCardsLimitsRows(limits)}</tbody>
          </table>
          {this.state.showEditModal ? (
            <EditLimitModal
              show={this.state.showEditModal}
              limitToEdit={this.state.limitToEdit}
              cardLimitsPeriod={cardLimitsPeriod}
              handleClose={this._toggleEditLimitModal}
            />
          ) : null}
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getLimits: () => {
    dispatch(getLimits());
  },
});

const mapStateToProps = state => ({
  cardLimits: state.Cards.cardLimits,
  selectedCard: getSelectedCard(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(LimitsTab),
);
