import React from 'react';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import CardOppositionIcon from '../../assets/SvgComponents/CardOppositionIcon/CardOppositionIcon';
import InputElement from '../../components/Ui/Input/Input';
import FormattedNumber from '../FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../utils/FormatsConfiguration';

const CardActions = props => (
  <div>
    <div className="price-total">
      <div className="solde--dispo animateds">
        <label htmlFor="Solde">{t('Components:availableBalance')}</label>
        <span>
          <FormattedNumber
            number={props.balance}
            format={FormatsConfiguration.AmountFormat}
            currency={t('Components:currency')}
          />
        </span>
      </div>
    </div>
    <div className="activation animateds">
      <div className="form-group">
        <InputElement
          elementType="SwitchLed"
          label={props.statusLabel}
          checked={props.cardActivated}
          onchange={props.onSwitchchange}
        />
      </div>
    </div>
    <div className="op animateds">
      <button className="button-opposer" onClick={props.onOpposeClick}>
        <div>
          <CardOppositionIcon />
        </div>
        <label className="col-sm-12 control-label" htmlFor="oppose">
          {t('Components:oppose')}
        </label>
      </button>
    </div>
  </div>
);

export default withTranslation()(CardActions);
