import React from 'react';
import { Link } from 'react-router-dom';
import PageHeader from '../../../components/PageHeader/PageHeader';
import PageHeaderNav from '../../../components/PageHeadNav/PageHeadNav';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { CARDS_NAMESPACE, TABS } from '../constants';
import CardsDetails from './cardsDetails';

const Feature = props => {
  const _getTabTitle = () => {
    let tab = TABS.filter(tab => tab.id === props.activeTab)[0];
    return tab.title;
  };
  const _renderActiveTab = () => {
    switch (props.activeTab) {
      case TABS[0].id:
        return <CardsDetails />;
      default:
        return null;
    }
  };
  return (
    <div className="page-content">
      <div className="page-content_side">
        <PageHeader text={t(`${CARDS_NAMESPACE}title`)}>
          <li key="1">
            <Link to="/dashboard">
              {t(`${CARDS_NAMESPACE}breadCrumbFirstLink`)}
            </Link>
          </li>
          <li key="2">
            <Link to="/cards">
              {t(`${CARDS_NAMESPACE}breadCrumbSecondLink`)}
            </Link>
          </li>
          <li className="active" key="3">
            {t(_getTabTitle())}
          </li>
        </PageHeader>
        <div className="page-content_inner">
          <PageHeaderNav>
            {TABS.map(tab => (
              <li key={tab.id}>
                <Link
                  className={tab.id === props.activeTab && 'active'}
                  title={t(tab.title)}
                  to="#"
                  onClick={() => props.activateTab(tab.id)}
                >
                  {t(tab.title)}
                </Link>
              </li>
            ))}
          </PageHeaderNav>
          <div className="main-content container-fluid">
            {_renderActiveTab()}
          </div>
        </div>
      </div>
    </div>
  );
};

export default withTranslation()(Feature);
