import React from 'react';
import classes from './Input.module.css';
import { Dropdown } from 'primereact/dropdown';
import { SelectButton } from 'primereact/selectbutton';
import { InputText } from 'primereact/inputtext';
import { InputSwitch } from 'primereact/inputswitch';
import Slider from '../Slider/Slider';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';

const template = option => <div>{option.label}</div>;
const input = props => {
  let inputElement = null;
  const inputClasses = ['form-control'];
  if (props.invalid && props.shouldValidate && props.touched) {
    inputClasses.push(classes.Invalid);
  }
  switch (props.elementType) {
    case 'DropDown':
      inputElement = (
        <>
          <label className="col-sm-3 control-label">{props.label}</label>
          <Dropdown
            className="col-sm-9"
            name={props.name}
            value={props.value}
            options={props.options}
            onChange={props.onchange}
            itemTemplate={template}
            filter={true}
            filterPlaceholder={props.filterPlaceholder}
            filterBy="label,value"
            showClear={true}
            required={true}
            showDelay={300}
            tooltipOptions={{ event: 'focus' }}
            tooltip={props.tooltip}
          />
        </>
      );
      break;
    case 'Choice':
      inputElement = (
        <>
          <label className="col-sm-3 control-label">{props.label}</label>
          <div className="col-sm-9">
            <SelectButton
              name={props.name}
              value={props.value}
              options={props.options}
              onChange={props.onchange}
            />
          </div>
        </>
      );
      break;
    case 'NumberInput':
      inputElement = (
        <>
          <label className="col-sm-3 control-label">{props.label}</label>
          <InputText
            className="col-sm-9 form-control"
            type="text"
            keyfilter="pint"
            name={props.name}
            value={props.value}
            options={props.options}
            onChange={props.onchange}
          />
        </>
      );
      break;
    case 'SwitchLed':
      inputElement = (
        <>
          <label className="col-sm-12 control-label">{props.label}</label>
          <InputSwitch checked={props.checked} onChange={props.onchange} />
        </>
      );
      break;
    case 'SwitchTvaSimulator':
      inputElement = (
        <div className="row-calcul">
          <label className="col-sm-12 control-label">{props.label}</label>
          <InputSwitch checked={props.checked} onChange={props.onchange} />
        </div>
      );
      break;
    case 'SimulatorSlider':
      let percentage =
        ((props.value - props.minValue) * 100) /
        (props.maxValue - props.minValue);
      if (props.value <= props.minValue) {
        percentage = 0;
      } else if (props.value >= props.maxValue) {
        percentage = 100;
      }

      let inputStyle = {
        left: `${percentage}%`,
      };
      inputElement = (
        <div className="simulator_item">
          <div className="col-xs-12">
            <h3>
              {`${props.inputField}: `}
              {props.value}
            </h3>
          </div>
          <div className="col-xs-12">
            <span className="min-price">
              <FormattedNumber
                number={props.minValue}
                format={FormatsConfiguration.AmountFormat}
              />
              {` ${props.unit}`}
            </span>
            <div className="slider-simulateur">
              <div className="form-group pull-right" style={inputStyle}>
                <InputText
                  value={props.value}
                  style={{ width: '14em' }}
                  type="number"
                  onChange={props.onchange}
                />
                <div className="devise">{props.unit}</div>
              </div>
              <Slider
                floatNumber={props.floatNumber}
                min={props.minValue}
                max={props.maxValue}
                step={props.step}
                value={props.value}
                onChange={props.onchange}
                style={{ width: '14em' }}
              />
            </div>
            <span className="max-price">
              <FormattedNumber
                number={props.maxValue}
                format={FormatsConfiguration.AmountFormat}
              />
              {` ${props.unit}`}
            </span>
          </div>
          {props.lock ? (
            <div className="p-svg p-plain">
              <input
                type="checkbox"
                checked={props.checked}
                onChange={props.onLockChange}
              />
              <label className="lock">Lock</label>
            </div>
          ) : null}
        </div>
      );
      break;
    default:
      inputElement = (
        <>
          <label className="col-sm-3 control-label">{props.label}</label>
          <input
            className="col-sm-9 form-control"
            {...props}
            value={props.value}
            onChange={props.onchange}
          />
        </>
      );
  }
  return <>{inputElement}</>;
};

export default input;
